$hover-color: rgb(16,199,216);
$background-color: rgb(4,18,48);
$focus-color:rgb(255,191,0);

.nav{
  height: 100vh;
  width: 250px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  background-color: $background-color;
  .nav__profile-logo{
    width: 250px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    img{
      height: 80%;
      border-radius: 50%;
      box-shadow:  0 0 5px whitesmoke;
    }
    p{
      text-align: center;
      color: whitesmoke;
      span{
        color: $focus-color;
      }
    }
  }
  .nav__items-container {
    height: 420px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .nav__links-container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 120px;
      #achi, #edu, #link {
        width: 190px;
        height: 32px;
        position: relative;
        color: whitesmoke;
        display: inline-block;
        text-decoration: none;
        text-transform: uppercase;
        &::after {
          content: "";
          position: absolute;
          width: 100%;
          height: 2px;
          background-color: $hover-color;
          bottom: 0;
          left: 0;
          transform: scaleX(0);
          transform-origin: bottom right;
          transition: transform 0.2s ease;
        }
        &:hover{
          color: $hover-color;
        }
        &:hover::after{
          transform-origin: bottom left;
          transform: scaleX(1);
        }
        &:focus{
          color: $focus-color;
        }
        &:focus::after{
          background-color: $focus-color;
          transform-origin: bottom left;
          transform: scaleX(1);
        }
      }
    }
  }
  .nav__social-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 200px;
    height: 90px;
    .nav__social-links{
      width: 100%;
      display: flex;
      justify-content: space-between;
      a{
        display: flex;
        align-items: center;
        justify-content: center;
      }
      a:hover{
        transition : transform 0.32s ease;
        transform: scale(1.3);
      }
    }
    p{
      color: rgba(255, 255, 255,.1);
      a{
        text-decoration: none;
        color: rgba(255, 255, 255,.1);
        &:hover{
          color: $hover-color;
        }
      }
    }
  }
}

@media screen and (max-width: 868px) {
  .nav{
    width: 100%;
    height: 180px;
    .nav__profile-logo{
      display: none;
    }
    .nav__items-container{
      height: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      .nav__links-container{
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 320px;
        height: 80px;
        #achi, #edu, #link{
          width: 100%;
        }
      }
    }
    .nav__social-container{
      height: 60px;
      width: 100%;
      p{
        margin-left: 70%;
      }
    }
  }
}