$hover-color: rgb(16,199,216);
$focus-color:rgb(255,191,0);
$background-color: rgb(4,18,48);

.achievements-desctop{
  margin: 3% 5% 0% 5%;
  display: flex;
  justify-content: space-around;
  color: whitesmoke;
  flex-wrap: wrap;
  .project{
    margin-top: 20px;
    padding: 6px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border: 2px solid rgba(4,18,48,.3);
    width: 235px;
    background-color: rgba(0,0,0, 0.4);
    border-top-left-radius: 15px;
    border-top-right-radius: 80px;
    text-align: center;
    h4{
      text-transform: uppercase;
    }
    p{
      line-height: 26px;
    }
    a{
      text-decoration: none;
      color: $focus-color;
      width: 100%;
      height: 25px;
    }
    &:hover{
      box-shadow: 5px 9px 15px black;
      transform: scale(1.1);
    }

  }img{
        width: 100%;
     }
}
@media screen and (max-width: 868px){
  .achievements-desctop{
    flex-wrap: wrap;
    .project{
      margin: 35px 0px 0px 0px;
      padding: 5px;
      height: 255px;
      width: 170px;
      font-size: 12px;
      align-items: flex-start;
      p{
        line-height: 16px;
      }
    }
  }
}